import Modal from 'flarum/components/Modal';
import app from 'flarum/app';
import Button from 'flarum/components/Button';
import Alert from 'flarum/common/components/Alert';

export default class PayRequestTransactionModal extends Modal {
  oninit(vnode) {
    super.oninit(vnode);
    this.bankAccountName = '';
    this.bankAccountNumber = '';
    this.bankName = '';
    this.totalAmount = 500000;
    this.bankList = [];
    this.loadingBanks = false;
    this.getBankName();
  }

  async getBankName() {
    this.loadingBanks = true;
    try {
      const response = await app.request({
        method: 'GET',
        url: 'https://api.vietqr.io/v2/banks',
      });
      console.log(response);

      this.bankList = response.data.map((bank) => ({
        value: bank.code,
        label: bank.name,
      }));
      console.log(this.bankList);
    } catch (error) {
      console.error('Error fetching bank list:', error);
      this.showAlert('error', 'Khng th ti danh sch ngn hng. Vui lng th li sau.', 5000);
    } finally {
      this.loadingBanks = false;
      m.redraw(); // Cp nht giao din
    }
  }

  title() {
    return 'Yu cu rt tin';
  }

  content() {
    return (
      <div className="Modal-body">
        <div className="Form-group">
          <label>Tng s tin c th rt</label>
          <input className="FormControl" type="text" value={this.totalAmount} disabled />
        </div>

        <div className="Form-group">
          <label>Tn ngn hng</label>
          {this.loadingBanks ? (
            <div>ang ti danh sch ngn hng...</div>
          ) : (
            <select
              className="FormControl"
              value={this.bankName}
              onchange={(e) => {
                this.bankName = e.target.value;
              }}
            >
              <option value="" disabled>
                Chn ngn hng
              </option>
              {this.bankList.map((bank) => (
                <option key={bank.value} value={bank.value}>
                  {bank.label}
                </option>
              ))}
            </select>
          )}
        </div>

        <div className="Form-group">
          <label>Tn ti khon</label>
          <input
            className="FormControl"
            type="text"
            placeholder="Nhp tn ti khon ngn hng"
            value={this.bankAccountName}
            oninput={(e) => {
              this.bankAccountName = e.target.value;
            }}
          />
        </div>

        <div className="Form-group">
          <label>S ti khon</label>
          <input
            className="FormControl"
            type="text"
            placeholder="Nhp s ti khon ngn hng"
            value={this.bankAccountNumber}
            oninput={(e) => {
              this.bankAccountNumber = e.target.value;
            }}
          />
        </div>

        <div className="Form-group">
          <Button className="Button Button--primary" type="submit" loading={this.loading}>
            Xc nhn
          </Button>
          <Button
            className="Button Button--secondary"
            onclick={() => {
              this.onCancelConfirmed();
              this.hide();
            }}
          >
            Hy b
          </Button>
        </div>
      </div>
    );
  }

  showAlert(type = 'success', message = '', timeClear = 5000) {
    app.alerts.show(Alert, { type: type }, message);
    setTimeout(() => {
      app.alerts.clear();
    }, timeClear);
  }

  onsubmit(event) {
    event.preventDefault();
    if (this.bankAccountName && this.bankAccountNumber && this.bankName && this.totalAmount) {
      const data = {
        rvn_bankacc_name: this.bankAccountName,
        rvn_bankacc_number: this.bankAccountNumber,
        rvn_bank_name: this.bankName,
        rvn_monney: this.totalAmount,
      };

      app
        .request({
          method: 'POST',
          url: app.forum.attribute('apiUrl') + '/pay-requests',
          body: { data },
        })
        .then((response) => {
          this.showAlert('success', 'Yu cu rt tin  c gi!', 5000);
          this.onCancelConfirmed();
          this.loading = false;
          app.modal.close();
        })
        .catch((error) => {
          console.log(error);
          this.showAlert('error', 'C li xy ra. Vui lng th li!', 5000);
          this.loading = false;
        });
    } else {
      this.showAlert('error', 'Thiu thng tin!', 2000);
      return;
    }
  }

  // X l khi hy b
  onCancelConfirmed() {
    this.bankAccountName = '';
    this.bankAccountNumber = '';
    this.bankName = '';
  }

  className() {
    return 'PayRequestTransactionModal';
  }
}
